body {
  font-family: "SFUIDisplay", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 200;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.title-lg {
  font-family: "Panton", Helvetica, Arial, sans-serif;
  font-size: 60px;
  line-height: 72px;
  letter-spacing: 0.25em;
  font-weight: 600;
}

.title-md {
  font-family: "Panton", Helvetica, Arial, sans-serif;
  font-size: 48px;
  line-height: 60px;
  letter-spacing: 0.25em;
  font-weight: 600;
}

.title-sm {
  font-family: "Panton", Helvetica, Arial, sans-serif;
  font-size: 30px;
  line-height: 48px;
  letter-spacing: 0.25em;
  font-weight: 600;
}

.title-xs {
  font-family: "SFUIDisplay", Helvetica, Arial, sans-serif;
  font-size: 18px;
  letter-spacing: 0.05em;
  font-weight: 200;
  color: #344c57;
}

.title-xs_small {
  font-size: 14px;
  font-weight: 600;
}

.inner {
  max-width: 1045px;
  padding: 0 20px;
  margin: 0 auto;
}

@media only screen and (max-width: 767px) {
  .inner {
    padding: 0 15px;
  }
}

.logo {
  width: 97px;
  display: block;
}

.logo img {
  max-width: 100%;
  display: block;
}

.icon-soc {
  background: #51e3c2;
  height: 40px;
  width: 40px;
  display: block;
  border-radius: 50%;
  position: relative;
}

.icon-soc .icon-icon_vk, .icon-soc .icon-icon_insta, .icon-soc .icon-icon_fb {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  fill: #fff;
}

.icon-master, .icon-visa, .icon-PayPay {
  font-size: 33px;
}

.dev-logo {
  display: flex;
}

.dev-logo span {
  margin-top: -5px;
  color: #839298;
  font-size: 10px;
  margin-right: 5px;
}

.dev-logo__icon {
  max-width: 85px;
  display: block;
}

.dev-logo__icon img {
  display: block;
  width: 100%;
}

.footer {
  padding: 55px 0 70px;
  background: #fafafa;
}

@media only screen and (max-width: 599px) {
  .footer {
    padding: 50px 0;
  }
}

.footer p {
  line-height: 30px;
}

@media only screen and (max-width: 599px) {
  .footer p {
    line-height: 16px;
    font-size: 12px;
  }
}

.footer__logo {
  margin-bottom: 70px;
}

@media only screen and (max-width: 1199px) {
  .footer__logo {
    margin-bottom: 200px;
  }
}

@media only screen and (max-width: 599px) {
  .footer__logo {
    display: none;
  }
}

.footer__top {
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 599px) {
  .footer__top {
    display: block;
  }
}

.footer__icon {
  width: 195px;
  flex: none;
}

@media only screen and (max-width: 1199px) {
  .footer__icon {
    width: 250px;
  }
}

@media only screen and (max-width: 599px) {
  .footer__icon {
    margin-bottom: 35px;
  }
}

.footer__info {
  display: flex;
  flex-wrap: wrap;
}

.footer__col {
  width: 195px;
}

@media only screen and (max-width: 1999px) {
  .footer__col {
    width: 50%;
  }
}

@media only screen and (max-width: 599px) {
  .footer__col_hide {
    display: none;
  }
}

@media only screen and (max-width: 599px) {
  .footer__col_xs {
    width: 50%;
  }
}

@media only screen and (max-width: 599px) {
  .footer__col_md {
    width: 100%;
    order: -1;
    display: flex;
    justify-content: space-between;
  }
}

.footer__subtitle {
  margin-bottom: 4px;
  line-height: 40px;
}

.footer__row {
  margin-bottom: 18px;
}

.footer__row_hide {
  display: block;
}

@media only screen and (max-width: 599px) {
  .footer__row_hide {
    display: none;
  }
}

.footer__row_show {
  display: none;
}

@media only screen and (max-width: 599px) {
  .footer__row_show {
    display: block;
  }
}

@media only screen and (max-width: 599px) {
  .footer__row_xs {
    width: 50%;
    margin-bottom: 32px;
  }
}

.footer__soc-icons {
  display: flex;
  justify-content: space-between;
  max-width: 140px;
  margin-top: 10px;
}

.footer__carts {
  display: flex;
  justify-content: space-between;
  max-width: 175px;
  padding-top: 13px;
}

.footer__nav {
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
}

.footer__nav li {
  line-height: 30px;
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .header-mobile {
  background-color: #51e3c2;
}

.header .header-mobile__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 26px 55px;
}

.header .header-mobile__menu-button-close {
  background: none;
  width: 37px;
  height: 37px;
  margin: 0 13px;
}

.header .header-mobile__menu-button-close img {
  width: 100%;
  height: 100%;
}

.header .header-mobile__menu {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header .header-mobile__menu-text {
  display: block;
  color: #344c57;
  font-family: "SFUIDisplay", Helvetica, Arial, sans-serif;
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.header .header-mobile__menu-text.let-spac {
  letter-spacing: 2.5px;
}

.header .header-mobile__length {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header .header-mobile__length-text {
  display: block;
  color: #344c57;
  font-family: "SFUIDisplay", Helvetica, Arial, sans-serif;
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.header .header-mobile__length-ru {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 26px;
  position: relative;
  border-radius: 50%;
  cursor: pointer;
  width: 40px;
  height: 40px;
  transition: background 0.3s;
}

.header .header-mobile__length-ru::after {
  display: block;
  position: absolute;
  content: '';
  width: 59px;
  height: 1px;
  top: 50%;
  left: 10%;
  background-color: #373e41;
}

.header .header-mobile__length-ru:hover::after {
  display: none;
}

.header .header-mobile__length-en {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 26px;
  position: relative;
  border-radius: 50%;
  cursor: pointer;
  width: 40px;
  height: 40px;
  background-color: #fff;
  transition: background 0.3s;
}

.header .header-mobile__length-en::after {
  display: block;
  position: absolute;
  content: '';
  width: 59px;
  height: 1px;
  top: 50%;
  right: 18%;
  background-color: #373e41;
  display: none;
}

.header .header-mobile__length-en:hover::after {
  display: none;
}

.header .header-mobile__navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 55px;
}

.header .header-mobile__item {
  background-color: #51e3c2;
  width: 100%;
  text-align: center;
  line-height: 84px;
}

.header .header-mobile__item.disp-768 {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header .header-mobile__item.border-first {
  width: 100%;
  border-top: 6px solid #ffffff;
}

.header .header-mobile__item.border-two {
  width: 100%;
  border-top: 5px solid #ffffff;
}

.header .header-mobile__item.border-third {
  width: 100%;
  border-top: 4px solid #ffffff;
}

.header .header-mobile__item.border-four {
  width: 100%;
  border-top: 3px solid #ffffff;
}

.header .header-mobile__item.border-five {
  width: 100%;
  border-top: 2px solid #ffffff;
}

.header .header-mobile__item.border-sex {
  width: 100%;
  border-top: 1px solid #ffffff;
}

.header .header-mobile__link {
  color: #344c57;
  font-family: "SFUIDisplay", Helvetica, Arial, sans-serif;
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-decoration: none;
  margin-left: -28px;
  border-bottom: 1px solid #51e3c2;
  transition: border 0.3s;
}

.header .header-mobile__link:hover {
  border-bottom: 1px solid #344c57;
}

